import axios from 'axios'
import {Message} from 'element-ui'

import router from '../../../router/index'
// 请求拦截器
axios.interceptors.request.use(request => {
  return request
}, (error) => {
  return Promise.reject(error)
})
// 响应拦截器
axios.interceptors.response.use(function(response) {
  return response
}, function(error) {
  // Do something with response error
  return Promise.reject(error)
})

export const postApi = (url, data, errCb) => {
  return axios.post(url, data || {})
    .then(res => {
      const data = res.data
      if (!res.data.success) {
        let msg = '出现了意料之外的错误'
        if (data.responseStatus.code === '9995') {
          // router.push('/Login')
          return Promise.reject(res)
        }
        if (data && data.responseStatus && data.responseStatus.message) {
          msg = data.responseStatus.message
        }
        // errCb ? errCb() : Message({
        //   message: msg,
        //   position: 'bottom',
        //   duration: 3000
        // });
        return Promise.reject(res)
      }
      return Promise.resolve(res)
    }, err => {
      let msg = '出现了意料之外的错误'
      if (err && err.data && err.data.responseStatus && err.data.responseStatus.message) {
        msg = err.data.responseStatus.message
      }
      errCb ? errCb() : Message({
        message: msg,
        position: 'bottom',
        duration: 3000
      });
      return Promise.reject(err)
    })
}
export default axios



