import Vue from 'vue'
import Router from 'vue-router'

const StartPage = () => import('@/view/StartPage/StartPage')//起始banner页

const Register = () => import('@/view/register/register') //注册
const registerInfo = () => import('@/view/register/registerInfo') //注册信息
const taobao = () => import('@/view/register/taobao') //验证淘宝账号
const success = () => import('@/view/register/success') //注册完成
const Login = () => import('@/view/login/login') //登录
const Index = () => import('@/view/Index/Index') //首页

const Home = () => import('@/view/Home/Home')//投放数据
//任务管理
const TaskList = () => import('@/view/TaskList/TaskList') //任务列表
const NewTask = () => import('@/view/TaskList/NewTask') //新建任务

const HistoricalTask = () => import('@/view/HistoricalTask/HistoricalTask') //历史任务
//系统管理
const AccountInfo = () => import('@/view/AccountInfo/AccountInfo') //账户信息

Vue.use(Router)

export default new Router({
  // mode:"history",
  base:__dirname,
  routes: [
    {
      //起始banner页
      path: '/startPage',
      name: '起始banner页',
      component: StartPage,
    },
    {
      //登录
      path: '/login',
      name: 'login',
      component: Login,
    },
    {
      //注册
      path: '/Register/registerInfo',
      name: 'Register',
      component: Register,
      children: [
        {
          //注册信息
          path: '/Register/registerInfo',
          name: 'registerInfo',
          component: registerInfo
        },
        {
          //验证淘宝账号
          path: '/Register/taobao',
          name: 'taobao',
          component: taobao
        },
        {
          //注册完成
          path: '/Register/success',
          name: 'success',
          component: success
        }
      ]
    },
    {
      //首页
      path: '/Index',
      name: 'Index',
      component: Index,
      children: [
        //任务管理
        {
          path: '/taskList',
          name: '任务列表',
          component: TaskList
        },
        {
          path: '/newTask',
          name: '新建任务',
          component: NewTask
        },
        {
          path: '/historicalTask',
          name: '历史任务',
          component: HistoricalTask
        },
        //系统管理
        {
          path: '/accountInfo',
          name: '账户信息',
          component: AccountInfo
        },
        //投放数据
        {
          path: '/home',
          name: '投放数据',
          component: Home
        },
        

      ]
    },
    {
      path: '/',
      name:"startPage",
      redirect: '/startPage',
      component: StartPage
    }
  ]
})



